import * as React from 'react';
import { useParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import Form, { useForm } from '@miq/formjs';
import { useRequest, Response } from '@miq/hookjs';
import { SharedDataCtx } from '@miq/contextjs';
import { Icons, Loading, TButtonProps } from '@miq/componentjs';

import { TSupplierOrder } from '../types';
import { ProductListFilterForm } from '../product';

import { SupplierOrder } from './models';
import SupplierProductList from './SupplierProductList';
import { SupplierOrderForm, SupplierOrderProductAddForm, SupplierOrderUpdateForm } from './forms';

const formFields: { [key: string]: any } = {
  name: '',
  order_id: '',
  stage: '',
  currency: 'USD',
  supplier: '',
  is_paid: false,
  total_cost: '',
};

export const mapDataToValues = (data: any = {}): TSupplierOrder => {
  const values: any = {};
  Object.keys(formFields).forEach((k) => {
    values[k] = data[`${k}`] || formFields[`${k}`];
  });
  return values;
};

export default function OrderUpdateView() {
  const { ordSlug } = useParams();

  const form = useForm<TSupplierOrder>(mapDataToValues());

  const { suppliers, currencies } = React.useContext(SharedDataCtx) as any;

  const { setValues } = form;
  const {
    res,
    loading,
    setLoading,
    setRes: setOrder,
    refresh: refreshOrder,
  } = useRequest(() => SupplierOrder({ slug: ordSlug }).retrieve(), {
    refreshDeps: [ordSlug],
    onSuccess: (res) => {
      document.title = `${res?.data?.name} - Update supplier order`;
      setValues(mapDataToValues(res.data));
    },
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const instance = SupplierOrder(r.data);

  // console.log(r.data.stages);

  if (!instance.supplier || !instance.currency)
    return (
      <Staff.View title="Modifier une commande" back="/staff/store/orders/">
        <Staff.Section className="d-flex justify-content-center mt-4">
          <SupplierOrderUpdateForm
            context={form}
            instance={instance}
            fields={['name', 'currency', 'supplier']}
            onSuccess={(res) => setOrder(res)}
          >
            <SupplierOrderForm.NameField required label="Nom" error={form.errors.currency} />
            <SupplierOrderForm.SupplierField
              required
              label="Supplier"
              suppliers={suppliers}
              error={form.errors.supplier}
            />
            <SupplierOrderForm.CurrencyField
              required
              label="Supplier Currency"
              currencies={currencies}
              error={form.errors.currency}
            />

            <Form.Submit value="Sauvegarder" className="btn-primary-3" />
          </SupplierOrderUpdateForm>
        </Staff.Section>
      </Staff.View>
    );

  return (
    <Staff.View
      title={form.values.name}
      back="/staff/store/orders/"
      actions={
        <Staff.NavLink to="settings/">
          <Icons.Gear />
        </Staff.NavLink>
      }
    >
      <div className="d-flex flex-column flex-xl-row">
        <div className="w-100">
          <Staff.Section header={<Header {...{ instance, setLoading, setOrder, categories: r?.data?.categories }} />}>
            {loading ? (
              <Loading icon />
            ) : (
              <SupplierProductList
                orderInstance={instance}
                categories={r?.data?.categories}
                stages={r?.data?.stages}
                refreshOrder={refreshOrder}
              />
            )}
          </Staff.Section>
        </div>

        <div className="min-w-35 ps-md-1">
          <DetailDisplaySection {...{ loading, instance }} />
        </div>
      </div>
    </Staff.View>
  );
}

const DetailDisplaySection = ({
  loading,
  instance,
}: {
  loading: boolean;
  instance: ReturnType<typeof SupplierOrder>;
}) => {
  return (
    <Staff.Section
      border
      className="bg-white mb-1"
      title={`Order Details${!loading && ` (${instance.products?.length})`}`}
      text={`Supplier: ${instance.supplier} | Currency: ${instance.currency}`}
    >
      <div className="mb-2">
        Categories
        <ul>
          {instance?.by_categories
            ?.filter((i) => i?.category__name)
            ?.map(({ category__name: name, ...i }) => (
              <DetailDisplaySection.Li title={name} key={name}>
                : {i.count}
              </DetailDisplaySection.Li>
            ))}
        </ul>
      </div>

      <ul className="mb-3">
        <DetailDisplaySection.Li title="Average cost: ">
          {instance.avg_cost} {instance.currency}
        </DetailDisplaySection.Li>
        <DetailDisplaySection.Li title="Total cost: ">
          {`${instance.total_cost || instance?.items_cost?.amountWithSymbol} ${instance.currency}`}
        </DetailDisplaySection.Li>
        <DetailDisplaySection.Li title="Est. revenue: ">{instance?.items_revenue?.amount}</DetailDisplaySection.Li>
      </ul>
    </Staff.Section>
  );
};

DetailDisplaySection.Li = ({ children, title }: { children: React.ReactNode; title: string }) => (
  <li>
    <span className="mr-2 text-muted">{title}</span>
    {children}
  </li>
);

const Header = ({ instance, setLoading, setOrder, categories }: any) => {
  const [tab, setTab] = React.useState<'filter' | 'add'>('filter');

  const render = () => {
    switch (tab) {
      case 'add':
        return (
          <SupplierOrderProductAddForm
            orderInstance={instance}
            onSuccess={(res) => {
              setLoading();
              setOrder(res);
            }}
          />
        );

      default:
        return <ProductListFilterForm categories={categories} fields={['category', 'published', 'presale']} />;
    }
  };

  return (
    <Staff.Section
      border
      title={
        <div>
          <HeaderButton tab={tab} onClick={() => setTab('filter')} active={tab === 'filter'}>
            <Icons.Funnel />
          </HeaderButton>
        </div>
      }
      actions={
        <HeaderButton tab={tab} onClick={() => setTab('add')} active={tab === 'add'}>
          <Icons.Plus />
        </HeaderButton>
      }
      className="bg-white mb-1"
    >
      <div className="" style={{ top: 0, position: 'sticky' }}>
        {render()}
      </div>
    </Staff.Section>
  );
};
const HeaderButton = ({
  children,
  tab,
  active,
  ...props
}: TButtonProps<{ tab: string; children: React.ReactNode; active: boolean }>) => {
  return (
    <Staff.Button
      className="btn-primary-3"
      style={{ border: active ? '1px solid var(--blue-600)' : undefined }}
      {...props}
    >
      {children}
    </Staff.Button>
  );
};
