import * as React from 'react';
import { useParams } from 'react-router-dom';

import './supplier-product-list.scss';
// import { TLang } from '@miq/utiljs';
// import pflang from '../Product/forms/lang';

import Staff from '@miq/staffjs';
import Form, { useForm } from '@miq/formjs';
import { useDocumentTitle, useRequest } from '@miq/hookjs';
import { Button, HorizontalScrollGridLayout, Icons, Loading, Pagination, ViewSection } from '@miq/componentjs';

import { PSTitle } from '../product/components';
import { StaffProduct, useProductListRequest, ProductDeleteButton } from '../product';
import { ProductForm, ProductQuickUpdateForm } from '../product/forms';
import { CategoryChoicesType, TProduct, TProductStageData } from '../types';

import { SupplierOrder } from './models';

// const i18n = (k: keyof TLang) => window.i18n(pflang, k);

export type SupplierProductListProps = {
  orderInstance: ReturnType<typeof SupplierOrder>;
  categories: CategoryChoicesType;
  stages: TProductStageData[];
  refreshOrder: Extract<ReturnType<typeof useRequest>, 'refresh'>;
};

export default function SupplierProductList({
  orderInstance,
  refreshOrder,
  categories,
  stages,
}: SupplierProductListProps) {
  useDocumentTitle('Supplier products');
  const { ordSlug } = useParams();
  const {
    res = { data: { results: [] } },
    loading,
    refresh,
  } = useProductListRequest({}, { supplier_order_slug: ordSlug });
  const [selected, setSelected] = React.useState<string[]>([]);
  const form = useForm({ category: '', is_pre_sale: '' });

  if (loading) return <Loading />;

  const isSelected = selected.length !== 0;

  return (
    <Staff.Section
      actions={
        isSelected && (
          <>
            <span className="p-1" onClick={() => setSelected([])} style={{ cursor: 'pointer' }}>
              [x]
            </span>
          </>
        )
      }
      footer={<Pagination {...res.data} component={Staff.NavLink} to />}
      className="supplier-p-list"
    >
      {res.data?.results.length ? (
        <>
          {isSelected && (
            <div className="d-grid grid-2 grid-md-4 gap-2" style={{ position: 'sticky', top: 0, zIndex: 1000 }}>
              <div className="">
                <Form
                  context={form}
                  onSubmit={(e) => {
                    e.preventDefault();
                    const { category } = form.values;
                    if (!category) return;

                    orderInstance
                      .updateCategories(category, selected)
                      .then((r) => {
                        refresh();
                        form.setValue('category', '');
                        setSelected([]);
                      })
                      .catch((err) => form.handleError(err));
                  }}
                >
                  <div className="d-flex">
                    {categories && <ProductForm.CategoryField categories={categories} error={form.errors.category} />}
                  </div>

                  <Form.Submit className="btn-primary">Update</Form.Submit>
                </Form>
              </div>

              <div>
                <Button
                  className="me-1"
                  onClick={() => {
                    orderInstance
                      .markPresale(selected)
                      .then(() => {
                        refresh();
                      })
                      .catch((err) => form.handleError(err));
                  }}
                >
                  Mark presale
                </Button>

                <Button
                  onClick={() => {
                    orderInstance
                      .unmarkPresale(selected)
                      .then(() => {
                        refresh();
                      })
                      .catch((err) => form.handleError(err));
                  }}
                >
                  unmark presale
                </Button>
              </div>
            </div>
          )}

          {res.data.results.map((item: TProduct) => (
            <SupplierProductListItem
              {...{ orderInstance, categories, selected, setSelected, refreshOrder, stages }}
              instance={StaffProduct(item)}
              key={item.slug}
            />
          ))}
        </>
      ) : null}
    </Staff.Section>
  );
}

type TSPListItemProps = {
  orderInstance: ReturnType<typeof SupplierOrder>;
  categories: CategoryChoicesType;
  stages: TProductStageData[];
  instance: ReturnType<typeof StaffProduct>;
  selected: string[];
  setSelected: React.Dispatch<React.SetStateAction<string[]>>;
  refreshOrder: () => Promise<null | undefined>;
};

const ItemCheckBox = ({ instance, selected, setSelected }: TSPListItemProps) => (
  <input
    type="checkbox"
    name="selected"
    className="me-2"
    checked={selected.includes(instance.slug!)}
    onChange={({ target }) => {
      const { checked } = target;
      if (checked) setSelected([...selected, instance.slug!]);
      else setSelected((selected) => selected.filter((i) => i !== instance.slug));
    }}
  />
);

const SupplierProductListItem = (props: TSPListItemProps) => {
  // Used for deleted items
  const [hide, setHide] = React.useState<boolean>(false);

  const { instance, categories } = props;

  const [open, setOpen] = React.useState(false);

  if (hide) return null;

  const { supplier_item, stage } = instance;
  const { orderInstance, stages = [] } = props;

  const stageData = stages?.find((i) => i.value === stage);

  return (
    <ViewSection
      border
      title={
        <div className="d-flex align-items-center">
          <ItemCheckBox {...props} />

          <div style={{ minWidth: 48, width: 48, height: 'auto' }} onClick={() => setOpen(!open)}>
            <Staff.Img.Thumb slug="slug" src="src" {...instance.cover_data} className="rounded" />
          </div>

          <div className="d-grid mx-1 text-sm">
            <PSTitle product={instance} className="" />

            <div className="">
              <div>
                {instance.is_on_sale && instance.sale_price && (
                  <span className="">
                    {instance.sale_price} {`<-`}{' '}
                  </span>
                )}
                <span className="">{instance.retail_price}</span>
                <span className="text-muted">
                  {' | '}
                  {supplier_item?.cost} {orderInstance.currency}
                </span>
              </div>

              <div className="">
                {instance.category_data?.name || ' - '}
                <span className="text-muted">
                  {' | '}
                  {supplier_item?.category || '-'}
                </span>
              </div>

              {stageData && <span className="text-muted">{stageData.label}</span>}

              <a className="ms-1" href="" target="_blank" rel="noopener noreferrer">{`↗️`}</a>
            </div>
          </div>
        </div>
      }
      actions={
        <div className="d-flex flex-column-reverse flex-md-row">
          <div className="">
            <ProductDeleteButton
              instance={instance}
              onSuccess={() => {
                setHide(true);
                props?.refreshOrder?.();
              }}
              className="btn-square"
            />
          </div>

          <div className="ms-md-1 mb-1">
            <Staff.Link to={`./${instance.slug}/`} className="btn btn-primary-3 btn-square">
              <Icons.Edit />
            </Staff.Link>
          </div>
        </div>
      }
      className="suppier-p-list-item bg-white"
    >
      {open && (
        <HorizontalScrollGridLayout className="form-tab no-scroll-bar gap-2">
          <ProductQuickUpdateForm instance={instance} categories={categories} orderSlug={orderInstance?.slug} />

          <div className="">
            <div className="mb-1">
              {`${supplier_item?.item_id ? <span className="">Item ID: {supplier_item?.item_id}</span> : ''}`}
            </div>
            <ul>
              {supplier_item?.category && (
                <li className="mb-1">
                  <div className="text-sm">Supplier Category</div>
                  <div className="text-muted">{supplier_item?.category}</div>
                </li>
              )}
              <li className="mb-1">
                <div className="text-sm">FOB Cost({orderInstance.currency})</div>
                <div className="text-muted">{supplier_item?.cost}</div>
              </li>

              {supplier_item?.url && (
                <li className="">
                  <a href={supplier_item?.url} target="_blank" className="text-underline" rel="noopener noreferrer">
                    View supplier product page
                  </a>
                </li>
              )}
            </ul>
          </div>
        </HorizontalScrollGridLayout>
      )}
    </ViewSection>
  );
};
