import * as React from 'react';
import { Route, Routes } from 'react-router-dom';

import './index.scss';

const ProductUpdateRoute = React.lazy(() => import('./views.Update'));
const ProductCreateView = React.lazy(() => import('./views.Create'));
const ProductListView = React.lazy(() => import('./views.List'));

export default function ProductRoutes() {
  return (
    <Routes>
      <Route path=":prodSlug/" element={<ProductUpdateRoute />} />
      <Route path="new" element={<ProductCreateView />} />
      <Route index element={<ProductListView />} />
    </Routes>
  );
}

